@import url(https://fonts.googleapis.com/css2?family=Advent+Pro:wght@200;400;500;700&display=swap);
button:focus {
  outline: 0;
  box-shadow: none;
}

body {
  font-family: 'Advent Pro', sans-serif;
  font-size: 16px;
  color: #666665;
}

h1 {
  font-size: 54px;
  font-weight: bold;
}

h2 {
  font-size: 40px;
  font-weight: 700;
}

h4 {
  font-size: 32px;
}

h5 {
  font-size: 24px;
}

img {
  display: block;
  width: 100%;
}

strong {
  font-weight: 700;
}

p {
  letter-spacing: 0.34px;
  line-height: 22px
}

.my-button {
  display: block;
  background-color: #FF576F;
  border-radius: 50px;
  padding: 8px 24px;
  font-size: 18px;
  font-weight: 500;
  color: white;
  text-transform: uppercase;
  text-align: center;
}

.my-button:hover {
  background-color: #FCCCD2;
  color: #FF576F;
  text-decoration: none;
}

.my-button.mini {
  padding: 8px 16px;
  font-size: 12px;

}

.my-button.mini.outlined {
  color: #FF576F;
  background-color: transparent;
  border: none;
  padding: 0;
  font-size: 12px;
}

.special-button {
  position: fixed;
  bottom: 24px;
  left: 50vw;
}

footer {
  height: 60px;
  width: 100%;
}

footer p {
  color: #B39898;
  font-size: 10px;
  font-weight: 500;
  letter-spacing: 0;
  line-height: 12px;
  text-align: center;
}

/* Modal style handler */

.modal-backdrop {
  background-color: #D9A5A6;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px)
}

.my-modal .modal-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 200px;
  border: 1px solid #D9A5A6;
  border-radius: 20px;
  background-color: #FFFFFF;
  padding: 32px;
}

.my-modal h3 {
  color: #B39898;
  font-weight: 700;
}

.my-modal p {
  width: 368px;
  margin: 16px 0;
}

.form-label {
  font-size: 14px;
  margin: 24px 0 4px;
}

.form-input {
  background-color: rgba(252, 204, 210, 0.1);
  border: none;
  border-bottom: solid #FCCCD2 2px;
  width: 368px;
}

.form-button {
  background-color: #FF576F;
  border-radius: 50px;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 16px;
  padding: 8px 24px;
  border: none;
  margin: 32px auto 16px;
  display: block;
  color: white;
}


.form-input::-webkit-input-placeholder {
