}


.form-input:-ms-input-placeholder {
  font-size: 12px;
}


.form-input::-ms-input-placeholder {
  font-size: 12px;
}


.form-input::placeholder {
  font-size: 12px;
}

.my-modal .mini-link {
  color: #6EA8B5;
  border: none;
}

.my-form .form-input.file-upload {
